<script>
import CONSTDATA from '../constData'
export default {
  name: 'HelloWorld',
  props: {},
  data(){
    return{
      tabIndex:'0',
      listDatas:CONSTDATA.NIUBI[0],
      activeName: 'first',
      niubi:CONSTDATA.NIUBI,
      data: [],
      value: [2,3],
    }
  },
  created(){},
  methods:{
    handleChange(){},
    //顶部导航栏点击事件
    handleClick(tab){
      this.tabIndex = tab.index;
      this.listDatas = this.niubi[tab.index];
    },
    //左侧菜单栏点击事件
    leftMenuClick(index){
      var generateData = () => {
        let datas = [];
        console.log(this.tabIndex,index);
        CONSTDATA.NIUBI[this.tabIndex].child[index].child.forEach((e,index) => {
          datas.push({
              label: `（`+index+`）` + e,
              key:index
          });
        })
        return datas;
      }
      this.data = generateData();
    },
    save(){
      localStorage.setItem('mydata',JSON.stringify(this.value));
    },

    handleOpen(key, keyPath) {
      console.log(key, keyPath);
    },
    handleClose(key, keyPath) {
      console.log(key, keyPath);
    },
  },
}
</script>


